.wpbnmpb2 {

    --title-font-size: 24px;
    --title-line-height: 1.2;
    --title-line-height-small: 1.2;
    --title-margin: 0 0 0.4em 0;
    --meta-font-size: 14px;
    --meta-font-size-small: 13px;
    --meta-spacing: 15px;
    --meta-margin: 0 0 15px;
    --category-font-size: 13px;
    --category-padding: 2px 5px;
    --category-bg-hover-color: #eee;
    --category-margin: 0 5px 5px 0;
    --excerpt-font-size: 16px;
    --excerpt-line-height: 1.5;
    --excerpt-font-size-small: 14px;
    --excerpt-line-height-small: 1.5;
    --excerpt-margin: 0 0 20px;
    --excerpt-margin-small: 0 0 20px;

    margin: 0 0 50px 0;

    .bnm-pb2-large-post {
        .entry-title {
            clear: none;
            font-size: var(--title-font-size);
            line-height: var(--title-line-height);
            letter-spacing: var(--title-letter-spacing);
            padding: var(--title-padding);
            margin: var(--title-margin);

            a {
                text-decoration: inherit;
                color: var(--title-color);
                
                &:hover {
                    color:  var(--title-hover-color);
                }
            }
        }

        .entry-meta,
        .entry-meta a {
            text-decoration: inherit;
            font-size: var(--meta-font-size);
            line-height: var(--meta-line-height);
            letter-spacing: var(--meta-letter-spacing);
        }

        .bnm-post-excerpt {
            color: var(--excerpt-color);
            font-size: var(--excerpt-font-size);
            line-height: var(--excerpt-line-height);
            letter-spacing: var(--excerpt-letter-spacing);
            padding: var(--excerpt-padding);
            margin: var(--excerpt-margin);
        }
    }

    .bnm-pb2-small-post {         
        .entry-title {
            clear: none;
            font-size: var(--title-font-size-small); 
            line-height: var(--title-line-height-small);
            letter-spacing: var(--title-letter-spacing-small);
            margin: 0 0 5px;

            a {
                text-decoration: inherit;
                color: var(--title-color);
                
                &:hover {
                    color:  var(--title-hover-color);
                }
            }
        }
        .entry-meta,
        .entry-meta a {
            font-size: var(--meta-font-size-small);
            line-height: var(--meta-line-height-small);
            letter-spacing: var(--meta-letter-spacing-small);
        }
        .bnm-post-excerpt {
            color: var(--excerpt-color);
            font-size: var(--excerpt-font-size-small);
            line-height: var(--excerpt-line-height-small);
            letter-spacing: var(--excerpt-letter-spacing-small);
            padding: var(--excerpt-padding-small);
            margin: var(--excerpt-margin-small);
        }
        .post-thumbnail {
            margin: 0 20px 0 0;
            width: 35%;
            float: left;
        }
        .entry-details {
            word-wrap: break-word;
        }
    }

    .bnm-category-list {
        a {
            color: var(--category-color);
            display: inline-block;
            font-size: var(--category-font-size);
            line-height: var(--category-line-height);
            letter-spacing: var(--category-letter-spacing);
            text-decoration: inherit;
            margin: var(--category-margin);

            &:hover {
                color: var(--category-hover-color);
            }
        }
    }

    &.bnm-box-cat {
		.bnm-category-list {
			a {
				background-color: var(--category-bg-color);
				padding: var(--category-padding);

				&:hover {
					background-color: var(--category-bg-hover-color);
				}
			}
		}
	}

    .entry-meta {
        display: flex;
        flex-wrap: wrap;
        gap: var(--meta-spacing);
        color: var(--meta-color);

        padding: var(--meta-padding);
        margin: var(--meta-margin);

        .bnm-post-author,
        .bnm-post-date,
        .bnm-comment-count {

            a {
                text-decoration: inherit;
                color: var(--meta-color);

                &:hover {
                    color: var(--meta-hover-color);
                }
            }
        }
    }

    .bnm-post-author {
        display: inline-block;
    }

    .post-thumbnail {
        margin: 0 0 17px 0;
        overflow: hidden;
    
        img {
            max-width: 100%;
            height: auto;
        }
    }

}

.bnm-category-list {
    display: block;
}

.bnm-avatar{
    margin-right: 5px;
    img {
        width: 25px;
        height: 25px;
        border-radius: 50%;
        vertical-align: top;
    } 
    
}

.bnm-row {
    display: flex;
    gap: 40px;
}

.bnm-pb2-posts-grid {
    display: grid;
    column-gap: 35px;
    row-gap: 20px;
    
    @media ( min-width: 480px ) {
        grid-template-columns: 1fr 1fr;
    }
}

.bnm-pb2-large-post,
.bnm-pb2-small-post {
    display: block;
    overflow: hidden;
    width: 100%;
}

/********************************************************************************************/
/**
Following CSS must go in a common CSS file.
*/
.thbnm-color-picker-container {
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;

    .thbnm-color-indicator {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        display: inline-block;
        padding: 0;
        background: linear-gradient(-45deg,transparent 48%,#ddd 0,#ddd 52%,transparent 0);
        box-shadow: inset 0 0 0 1px rgb(0 0 0 / 20%);
    }

    .thbnm-color-control-click-side {
        display: flex;
        align-items: center;
    }
}

.thbnm-typograpy-control-button {
    margin: 0;
    height: 28px;
    width: 28px;
    padding: 5px;
    border: 1px solid #E6E7E9;
    border-radius: 14px;
    color: #50575E;
}

.thbnm-typography-control-container {
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
}

.thbnm-typography-controls-wrapper {
    width: 260px;
    padding: 10px;
}

.thbnm-settings-panel .block-editor-panel-color-gradient-settings.block-editor-panel-color-gradient-settings {
    padding-left: 0;
    padding-right: 0;
    border-top: none;
}

.thbnm-tab-panel {

    .components-tab-panel__tabs-item {
        &:focus:not(:disabled) {
            box-shadow: inset 0 var(--wp-admin-border-width-focus) var(--wp-admin-theme-color);
        }
        
        &.thbnm-active-tab {
            box-shadow: inset 0 var(--wp-admin-border-width-focus) var(--wp-admin-theme-color);
        }
    }

    .components-tab-panel__tab-content {
        padding: 8px;
        border: 1px solid #ccc;
    }

    padding-bottom: 8px;
    border-bottom: 1px solid #ddd;
}

.block-editor-block-inspector .thbnm-panel .components-base-control.components-toggle-control {
    margin: 0;
}

.block-editor-block-inspector .thbnm-panel .components-base-control.components-toggle-control:last-child {
    margin-bottom: 8px;
}

